/* CSS import */
import style from "../../styles/AccountItem.module.css";

function AccountItem() {
  return (
    <>
      <div className={style.wrap}>
        <div className={style.profile_wrap}>
          <div className={style.image_wrap}>
            <img
              src={`${process.env.PUBLIC_URL}/images/memogi02.png`}
              alt="profile"
            />
          </div>
          <ul className={style.profile}>
            <li>
              NAME :<span> (Kim So Jin)</span>
            </li>
            <li>
              BIRTH :<span>1997. 03. 11</span>
            </li>
            <li>
              ADDRESS :<span> </span>
            </li>
            <li>CERTIFICATE</li>
            <li>
              -<span>2018 . 04 GTQ() 1</span>
            </li>
            <li>
              -<span>2017 . 11  2</span>
            </li>
            <li>
              MBTI :<span>ISTJ</span>
            </li>
            <li># # #</li>
          </ul>
        </div>
      </div>
    </>
  );
}

export default AccountItem;
